import { useState, useEffect} from 'react';
import {
    Form,
    Input,
    Select,
    Button,
    InputNumber,
    Row,
    Col,
    DatePicker,
    TimePicker,
    Slider,
    Alert, 
    Layout,
    Spin} from 'antd';
    
const  {Content} = Layout ;
const { RangePicker } = TimePicker;
const { TextArea } = Input;

// Styles
import styles from "../../styles/create_admin.module.css";
import {
    ArrowLeftOutlined,
} from '@ant-design/icons';
import { Link } from 'react-router-dom';
import { request ,requestDelete, createRequest } from '../../services/request';
import { get, isEmpty } from 'lodash';
import { urls } from '../../services/URLs'; 

// View
import CandidateFeedbackStats from './CandidateFeedbackStats';

const { Option } = Select;

const formItemLayout = {
    labelCol: {
        xs: {
            span: 24,
        },
        sm: {
            span: 8,
        },
    },
    wrapperCol: {
        xs: {
            span: 24,
        },
        sm: {
            span: 16,
        },
    },
};

const tailFormItemLayout = {
    wrapperCol: {
        xs: {
            span: 24,
            offset: 0,
        },
        sm: {
            span: 16,
            offset: 8,
        },
    },
};

const prefixSelector = (
    <Form.Item name="prefix" noStyle>
        <Select
            style={{
                width: 70,
            }}
        >
            <Option value="91">+91</Option>
        </Select>
    </Form.Item>
);

function EditCandidate({ id, from }) {
    const [form] = Form.useForm();
    const [expInMonth, setExpInMonth] = useState(0);
    const [expInYear, setExpInYear] = useState(0);
    const [relExpInMonth, setRelExpInMonth] = useState(0);
    const [relExpInYear, setRelExpInYear] = useState(0);

    const [interviewDate, setInterviewDate] = useState();
    const [interviewTime, setInterviewTime] = useState([]);
    const [initData,setinitData] = useState([])
    const [isupdated,setisupdated] = useState({})
    const [isload,setisload] = useState(false)

    const [isFromInterviewer, _] = useState(!!from);
    const onFinish = (values) => {
        setisload(true)
        // values['experience'] = `${expInYear} years ${expInMonth} months`;
        // values['Relavant Experience'] = `${relExpInYear} years ${relExpInMonth} months`;
        // values['interviewDate'] = interviewDate;
        // values['interviewTime'] = interviewTime;
        console.log('Received values of form: ', values);
        
        const payload ={
            'id':`${id}`,
            ...values
        }
        const finalUrl = get(urls[0], "GET_CANDIDATES");

        createRequest(finalUrl, 'put', payload, id ).then(res => {
            setisupdated({
                message:"Candidate Updated succesfully",
                type:"success"
            })
            setisload(false)
        }).catch(() => {
            console.error
            setisupdated({
                message:"Candidate Update failed",
                type:"error"
            })
            setisload(false)
        });
    };
    function handleChange(value) {
        console.log(`selected ${value}`);
    }
    const handleClose = () => {
        setisupdated({});
      };

    useEffect(() => {
        setisload(true)
        request(get(urls[0], "GET_CANDIDATES") + `/${id}`).then((res) => {
            setinitData(res.data)
            setinitData((state) => {
                form.setFieldsValue({
                    ...state
                })
                setisload(false)
            })

        }, [])
    }, [])

    return (
        <Layout>
            <Spin spinning={isload}>            
            <Content>
                <Row>
                    <Col span={isFromInterviewer ? 24 : 15} offset={isFromInterviewer ? 3 : 3}>
                    {!isEmpty(isupdated) && <Alert message={isupdated.message} type={isupdated.type} closable afterClose={handleClose} />}
                    </Col>
                
                </Row>
           
            <Row>
                
                <Col span={isFromInterviewer ? 24 : 15} offset={isFromInterviewer ? 4 : 2}>
                    <div>
                        <Link to={isFromInterviewer ? "/interviewer" : "/admin"}>
                            <ArrowLeftOutlined style={{ fontSize: '20px', color: '#08c', cursor: "pointer", margin:'30px 0'}} />
                            Back
                        </Link>
                    </div>
                    <Form
                        {...formItemLayout}
                        form={form}
                        name="create_candidate_form"
                        onFinish={onFinish}                     
                        scrollToFirstError
                        className={styles.create_admin_form}
                        style={!isFromInterviewer ? { width: "80%" } : {}}
                    >
                        {/* <Form.Item
                            name="candidateId"
                            label="Candidate Id"
                        >
                            <Input disabled />
                        </Form.Item> */}

                        <Form.Item
                            name="firstName"
                            label="First Name"
                            rules={[
                                {
                                    message: 'Please enter valid First Name!',
                                    whitespace: true,
                                    max:50
                                },
                            ]}
                        >
                            <Input   />
                        </Form.Item>

                        <Form.Item
                            name="lastName"
                            label="Last Name"
                            rules={[
                                {
                                    message: 'Please enter valid Last Name!',
                                    max:50
                                }
                            ]}
                        >
                            <Input   />
                        </Form.Item>

                        <Form.Item
                            name="phoneNumber"
                            label="Candidate Mobile no"
                            rules={[
                                {
                                    required: true,
                                    message: 'Please input valid Candidate mobile number!',
                                    max:10,
                                    min:10
                                },
                            ]}
                        >
                            <Input
                                addonBefore={prefixSelector}
                                style={{
                                    width: '100%',
                                }}
                                type='number'
                            />
                        </Form.Item>

                        <Form.Item
                            name="email"
                            label="Candidate E-mail"
                            rules={[
                                {
                                    type: 'email',
                                    message: 'The input is not valid E-mail!',
                                },
                                {
                                    required: true,
                                    message: 'Please input your E-mail!',
                                },
                            ]}
                        >
                            <Input   />
                        </Form.Item>

                        <Form.Item
                            name="skills"
                            label="Skill Sets"
                            tooltip="Write skills with comma"
                            rules={[
                                {
                                    required: true,
                                    message: 'Please enter Skill Sets!',
                                    whitespace: true,
                                },
                            ]}
                        >
                            <Input   />
                        </Form.Item>

                        <Form.Item
                            name="jobTitle"
                            label="Job Title"
                            rules={[
                                {
                                    required: true,
                                    message: 'Please enter Job Title!',
                                    whitespace: true,
                                    max: 50
                                },
                            ]}
                        >
                            <Input   />
                        </Form.Item>

                        <Form.Item
                            name="jobDescription"
                            label="Job Description"
                            rules={[
                                {
                                    required: true,
                                    message: 'Please Enter description !'
                                },
                            ]}
                        >
                            <TextArea rows={3} />
                        </Form.Item>

                        <Form.Item
                            name="experience"
                            label="Total Experience"
                            rules={[]}
                        >
                            <Input.Group size="large">
                                <Row gutter={16}>
                                    <Col span={10}>
                                        <InputNumber 
                                        onChange={(value) => setExpInYear(value)} 
                                        addonAfter="Years" 
                                        value={expInYear} 
                                        min="0"
                                        step="0.1"  />
                                    </Col>
                                    {/* <Col span={10}>
                                        <InputNumber onChange={(value) => setExpInMonth(value)} addonAfter="Months" value={expInMonth}   />
                                    </Col> */}
                                </Row>
                            </Input.Group>
                        </Form.Item>

                        <Form.Item
                            name="relavantExperience"
                            label="Relavant Experience"
                            rules={[]}
                        >
                            <Input.Group size="large">
                                <Row gutter={16}>
                                    <Col span={10}>
                                        <InputNumber onChange={(value) => setRelExpInYear(value)} addonAfter="Years" value={relExpInYear}  min="0"
                                        step="0.1"  />
                                    </Col>
                                    {/* <Col span={10}>
                                        <InputNumber onChange={(value) => setRelExpInMonth(value)} addonAfter="Months" value={relExpInMonth}   />
                                    </Col> */}
                                </Row>
                            </Input.Group>
                        </Form.Item>

                        {/* <Form.Item
                            name="status"
                            label="Candidate Status"
                            rules={[]}
                        >
                            <Select onChange={handleChange}>
                                <Option value="Round1">Round1</Option>
                                <Option value="Round2">Round2</Option>
                                <Option value="Round3">Round3</Option>
                            </Select>
                        </Form.Item> */}

                        {/* <Form.Item
                            name="InterviewerName"
                            label="Interviewer Name"
                            rules={[
                                {
                                    required: true,
                                    message: 'Please enter Interviewer Name !',
                                    whitespace: true,
                                }
                            ]}
                        >
                            <Input />
                        </Form.Item>
                        <Form.Item
                            name="interviewerEmail"
                            label="Interviewer E-mail"
                            rules={[
                                {
                                    required: true,
                                    message: 'Please enter Interviewer Email !',
                                    whitespace: true,
                                }
                            ]}
                        >
                            <Input  />
                        </Form.Item>
                        <Form.Item
                            name="interviewDate"
                            label="Interview Date"
                            rules={[
                                {
                                    required: true,
                                    message: 'Please Enter Interview Date'
                                },
                            ]}
                        >
                            <DatePicker onChange={(date, dateString) => setInterviewDate(dateString)} style={{ width: "100%" }}   />
                        </Form.Item>

                        <Form.Item
                            name="interviewTime"
                            label="Interview Time"
                            rules={[
                                {
                                    required: true,
                                    message: 'Please Enter Interview Time'
                                },
                            ]}
                        >
                            <RangePicker onChange={(date, dateString) => setInterviewTime(dateString)} style={{ width: "100%" }}  />
                        </Form.Item>

                        <Form.Item
                            name="invitationLink"
                            label="Invitation Link"
                            rules={[
                                {
                                    required: true,
                                    message: 'Please Enter valid invitation link'
                                },
                            ]}
                        >
                            <Input />
                        </Form.Item> */}
                        {/* {
                            isFromInterviewer && (
                                <>
                                    <Form.Item
                                        name="technicalSkills"
                                        label="Technical Skills"
                                        tooltip="On Scale of 1-10"
                                        rules={[]}
                                    >
                                        <Slider max={10} />
                                    </Form.Item>
                                    <Form.Item
                                        name="communicationSkills"
                                        label="Communication Skills"
                                        tooltip="On Scale of 1-10"
                                        rules={[]}
                                    >
                                        <Slider max={10} />
                                    </Form.Item>
                                    <Form.Item
                                        name="analyticalSkills"
                                        label="Analytical Skills"
                                        tooltip="On Scale of 1-10"
                                        rules={[]}
                                    >
                                        <Slider max={10} />
                                    </Form.Item>
                                    <Form.Item
                                        name="realTimeDebuggingSkills"
                                        label="Real time Debugging Skills"
                                        tooltip="On Scale of 1-10"
                                        rules={[]}
                                    >
                                        <Slider max={10} />
                                    </Form.Item>
                                    <Form.Item
                                        name="confidenceLevels"
                                        label="Confidence levels"
                                        tooltip="On Scale of 1-10"
                                        rules={[]}
                                    >
                                        <Slider max={10} />
                                    </Form.Item>
                                    <Form.Item
                                        name="previousProjects"
                                        label="Previous Projects"
                                        tooltip="On Scale of 1-10"
                                        rules={[]}
                                    >
                                        <Slider max={10} />
                                    </Form.Item>

                                    <Form.Item
                                        name="feedback"
                                        label="Feedback"
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Please Enter Feedback !'
                                            },
                                        ]}
                                    >
                                        <TextArea rows={4} />
                                    </Form.Item>
                                </>
                            )
                        } */}
                        {/* <Form.Item
                                        name="feedback"
                                        label="Feedback"
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Please Enter Feedback !'
                                            },
                                        ]}
                                    >
                                        <TextArea rows={4} />
                                    </Form.Item> */}
                        <Form.Item {...tailFormItemLayout}>
                            <Button type="primary" htmlType="submit">
                                Edit candidate
                            </Button>
                        </Form.Item>
                    </Form>
                </Col>
                {/* {
                    !isFromInterviewer && (
                        <Col span={7}>
                            <CandidateFeedbackStats />
                        </Col>
                    )
                } */}
            </Row>
            </Content>
            </Spin>
        </Layout>
       

    )
}

export default EditCandidate;