// Packages
import {
  useState,
  useEffect,
  useContext
} from 'react';

import {
  Form,
  Input,
  Button,
  Select,
  message,
  Row,
  Col
} from 'antd';

import { AzureAD, AuthenticationState } from 'react-aad-msal';
import {
  UserOutlined,
  LockOutlined
} from '@ant-design/icons';
import { authProvider } from '../AdLogin/authProvider';

import { withRouter, Link } from 'react-router-dom';

// Styles
import styles from "../../styles/login.module.css";

// Context
import { RootContext } from "../../Context/RootContext";

// Utils
import { removeFromSession, setToSession } from "../../utils/storage";
import Signup from '../Signup/Signup';
import { requestPost } from '../../services/request';
import { get } from 'lodash';
import { urls } from '../../services/URLs';
import { microsoftLoginPath } from '../../Constants/constants';

const Login = props => {
  const { Option } = Select;
  const [form] = Form.useForm();
  const [, forceUpdate] = useState({}); // To disable submit button at the beginning.
  const { setIsLogedIn, setInterviewerDataWhenLogin, SetRole } = useContext(RootContext);
  const [newUser, setNewUser] = useState(false);

  useEffect(() => {
    const loginToken = sessionStorage.getItem(
      "msal.ba6f6865-88a9-43b7-ac2b-b002b3168225.idtoken"
    );
    forceUpdate({});
    if(loginToken){
      loginWithToken(loginToken)
    }else{
      sessionStorage.removeItem("userName");
      setIsLogedIn(false)
      removeFromSession("user_loged_in")
    }
  }, []);

  const onFinish = async ({ userName, password, type }) => {
    const payload = { userName, password, type:type.toLowerCase() };
    const res = await requestPost(get(urls[0], "LOGIN_SERVICE"), payload);
    if(res.data){
      setToSession("user_loged_in",true);
      if (type === 'Interviewer') {
        SetRole("interviewer")
        props.history.push("/interviewer")
        setToSession("user_loged_in", "interviewer")
        setInterviewerDataWhenLogin({id:res.data.id, userName: res.data.userName})
        sessionStorage.setItem("user_id", res.data.id)
        sessionStorage.setItem("userName", res.data.userName)
      } else if (type === 'Admin') {
        SetRole("Admin")
        setToSession("user_loged_in", "admin")
        props.history.push("/admin")
      }
    }else{
      message.error('please provide valid credentials')
    }
  };

  const authHandler = (err, data) => {
    console.log("data", data);
    console.log("err", err);
  };

  const onFinishFailed = (errorInfo) => {
    message.error('This is an error message');
    console.log('Failed:', errorInfo);
  };

  const onRoleSelect = (value) => {
    switch (value) {
      case 'Admin':
        form.setFieldsValue({
          note: 'Hi, Admin!',
        });
        return;

      case 'Interviewer':
        form.setFieldsValue({
          note: 'Hi Interviewer!',
        });
    }
  };

  const loginWithToken = async (loginToken) => {
    const payload = loginToken;
    let options = {
      method: "POST",
      body: payload,
    };
    let res = fetch(
      "http://portal.digisprint-lp.com:8081/interviewer/loginWithToken",
      options
    );
    res
      .then((res) => res.json())
      .then((res) => {
        console.log("res", res);
        if (res.id) {
          if (res.admin === true) {
            SetRole("Admin");
            setToSession("user_loged_in", "admin")
            props.history.push("/admin");
          } else {
            SetRole("interviewer");
            setToSession("user_loged_in", "interviewer")
            props.history.push("/interviewer");
          }
          if(res.hr === true){
            setToSession("isHr", "true");
          }if(res.selector === true){
            setToSession("selector", "true");
          }
          message.success("logged in successfully.")
          setIsLogedIn(true);
          // setToSession("user_loged_in", true);
          sessionStorage.setItem("user_id", res.id);
          sessionStorage.setItem("userName", res.interviewerName);
        }else{
          message.error("you don't have access to use this portal.")
        }
      });
  };

  // const loginToken = sessionStorage.getItem(
  //   "msal.ba6f6865-88a9-43b7-ac2b-b002b3168225.idtoken"
  // );
  // if (loginToken) {
  //   loginWithToken(loginToken);
  // }

  return (
    !newUser ?
    (
      <>
        <Form
          form={form}
          name="basic"
          labelCol={{
            span: 8,
          }}
          wrapperCol={{
            span: 16,
          }}
          initialValues={{
            remember: true,
          }}
          onFinish={onFinish}
          onFinishFailed={onFinishFailed}
          autoComplete="off"
        >
          <Form.Item
            name="type"
            label="Role"
            rules={[
              {
                required: true,
              },
            ]}
          >
            <Select
              placeholder="Select Role"
              onChange={onRoleSelect}
              allowClear
            >
              <Option value="Admin">Admin</Option>
              {/* <Option value="Super-Admin">Super Admin</Option> */}
              <Option value="Interviewer">Interviewer</Option>
            </Select>
          </Form.Item>

          <Form.Item
            name="userName"
            label="username"
            rules={[
              {
                required: true,
                message: 'Please input your username!',
              },
            ]}
          >
            <Input prefix={<UserOutlined className="site-form-item-icon" />} placeholder="Username" />
          </Form.Item>

          <Form.Item
            name="password"
            label="password"
            rules={[
              {
                required: true,
                message: 'Please input your password!',
              },
            ]}
          >
            <Input.Password
              prefix={<LockOutlined className="site-form-item-icon" />}
              type="password"
              placeholder="Password"
            />
          </Form.Item>

          <Form.Item shouldUpdate>
            {() => (
              <Button
                type="primary"
                htmlType="submit"
                disabled={
                  !form.isFieldsTouched(true) ||
                  !!form.getFieldsError().filter(({ errors }) => errors.length).length
                }
                className={styles.login_button}
              >
                Log in
              </Button>
            )}
          </Form.Item>
        </Form>

        {/* commented code for signup form */}
        {/* <Row style={{alignItems:"center"}}>
          <Col>
            Not a logged in user?
          </Col>
          <Col>
            <Button type="link" onClick={()=>setNewUser(true)}>Signup</Button>
          </Col>
        </Row> */}

        <Row style={{alignItems:"center"}}>
          <Col style={{alignItems:"center"}}>
          <AzureAD provider={authProvider } forceLogin={false} >
          {({ accountInfo, authenticationState, error, login, logout }) => {
              return(<div>
                {console.log(authenticationState)}
                {console.log(accountInfo)}
                <button className={styles.microsoft_login} onClick={login}>
                    <img className={styles.microsoft_logo} alt="svgImg" src={microsoftLoginPath}/> Sign in With Microsoft
                </button>
              </div>)
          }}
          </AzureAD>
          </Col>
        </Row>
      </>
    )
  : <Signup setNewUser={setNewUser} />
  )
}

export default withRouter(Login);
