import { UserOutlined, DownloadOutlined, ContactsOutlined, EditOutlined, ArrowLeftOutlined, DatabaseOutlined } from "@ant-design/icons";
import { Avatar, Button, Card, Col, Descriptions, Divider, Layout, message, Row, Spin, Timeline, Typography } from "antd";
import { get, isEmpty } from "lodash";
import FileSaver from "file-saver";
const { Title, Paragraph } = Typography
const { Content } = Layout
import { react, Component } from "react";
import { request, requestPdf } from "../../services/request";
import { urls } from "../../services/URLs";
import './Candidate.css'
import {
    useParams
} from 'react-router-dom';
import CandidateForm from "../Admin/CandidateForm";
import { Link } from "react-router-dom";
import Candidateworkflow from './CandidateFlow/Candidateworkflow'
import { Router } from "react-router-dom";
import { RootContext } from "../../Context/RootContext";


class ViewCandidate extends Component {
    static contextType = RootContext
    constructor(props) {
        super(props);
        this.state = {
            candidate_id: "",
            candidate: {},
            isload: false,
            selectorData: {},
        };
    }

    componentDidMount() {
        const { candidate_id } = this.props.match.params
        this.setState({
            candidate_id
        })
        this.getcandidates(candidate_id)
    }



    getcandidates = async (id) => {
        try {
            this.setState({
                isload: true
            })
            const url = get(urls[0], "DELETE_CANDIDATE") + `${id}`
            const res = await request(url)
            console.table(res.data)
            if (res) {
                this.setState({
                    candidate: res.data
                })
                this.setState({
                    isload: false
                })
            }
        }
        catch (e) {
            this.setState({
                isload: false
            })
            console.error("error", e)
            message.error("Error loading data" + e)
        }
    }

    refreshtheCandidateData = () => {
        const { candidate_id } = this.state
        this.getcandidates(candidate_id)
    }

    setLoader = (value) => {
        this.setState({isload: value});
    }

    downloadResume = async (candidate) => {
        const { candidate_id } = this.state
        const finalUrl = `${get(urls[0], "GET_CANDIDATES")}/download/${candidate_id}`;
        const res = await requestPdf(finalUrl);
        var view = new Uint8Array(res.data);      
        let blob = new Blob([view], {type : 'application/octet-stream'});
        const docType = candidate.resume.split(".");
        FileSaver.saveAs(blob,candidate.firstName + `.${docType[1]}` );
    }

    render() {
        const { candidate, isload, candidate_id } = this.state;
        let  {role}  = this.context;
        return (<RootContext.Provider>
            <Layout>
                <Content style={{ margin: "30px" }}>
                    <Spin spinning={isload}>

                        <Row>
                            <Link to={`/${sessionStorage.getItem("user_loged_in")}` }style={{ margin: "20px", textDecoration: "underline", fontSize: "16px" }}> <span><ArrowLeftOutlined /> Back</span></Link>
                        </Row>



                        <Row gutter={[24, 24]}>
                            <Col span={8} className="viewProfileCards">
                                <Row gutter={[24, 24]}>
                                    <Col className="avatar-align" >
                                        <Avatar size={80} icon={<UserOutlined />} />
                                        <Title level={4} strong>{candidate.firstName + " " + candidate.lastName}

                                            <span style={{ padding: "0 20px" }}>
                                            {sessionStorage.getItem("user_loged_in") ===
                                                "admin" && (
                                                <Link
                                                to={`/admin/edit_candidate/${candidate_id}`}
                                                >
                                                <EditOutlined />
                                                </Link>
                                            )}
                        
                                            </span> </Title>

                                    </Col>
                                </Row>
                                <Row type="flex" gutter={[24, 24]} className="topspacing">
                                    <Col span={24} style={{ height: "100%" }}>
                                        <Title level={5}  > <ContactsOutlined /> <span style={{ padding: "10px" }}>Personal Info</span></Title>
                                        <Card
                                        >   <Paragraph>Email : <span> {candidate.email}</span></Paragraph>
                                            <Divider />
                                            <Paragraph>Phone : <span> {candidate.phoneNumber}</span></Paragraph>

                                        </Card>

                                    </Col>
                                    <Col span={24} style={{ height: "100%" }}>
                                        <Title level={5}  > <DatabaseOutlined /> <span style={{ padding: "10px" }}>Professional Background</span></Title>
                                        <Card  >
                                            <Paragraph>Total Experience : <span>{candidate.experience}{" "}Years</span></Paragraph>
                                            <Divider />
                                            <Paragraph>Relevant Experience : <span>{candidate.relavantExperience}{" "}Years</span></Paragraph>
                                            {/* <Divider />
                                        <Paragraph>Job Title : <span>{candidate.jobTitle}{" "}Years</span></Paragraph> */}

                                        </Card>
                                    </Col>

                                    {/* <Col span={24} >
                                        <Title level={5}  > <FundProjectionScreenOutlined /> <span style={{ padding: "10px" }}>Technical Skills</span></Title>
                                        <Card >

                                            <ol>
                                                {this.state.candidate?.skills?.split(",").map((key, value) => {
                                                    return (
                                                        <li id={key}>{key}</li>
                                                    )
                                                })}
                                            </ol>
                                        </Card>

                                    </Col> */}


                                </Row>
                            </Col>

                            <Col span={8}  push={1}>
                                <Row className="topMargin">
                                    {!isEmpty(candidate) && <Candidateworkflow
                                        refreshtheCandidateData={() => { this.refreshtheCandidateData() }}
                                        candidate={candidate} 
                                        selector = {this.state.selectorData}
                                        role={role}
                                        setLoader={this.setLoader}
                                        />
                                        
                                        }
                                </Row>
                            </Col>
                            <Col span={8} >
                                <Row className="topMargin" gutter={[24,24]}>
                                <Col span={24} className="align-left"> 
                                    <Button type="primary" shape="round" onClick={()=>{this.downloadResume(candidate)}} icon={<DownloadOutlined />} >Download Resume</Button>
                                    </Col>
                                    <Col span={24}>
                                    <Title level={5}  > <ContactsOutlined /> <span style={{ padding: "10px" }}>Job  Description</span></Title>
                                    <Card>
                                       <Paragraph>
                                       {candidate.jobDescription}
                                       </Paragraph>
                                   </Card>
                                    </Col>
                                  
                                </Row>
                            </Col>


                            {/* <Col span={8} style={{ margin: "auto " }}>
                                <Row gutter={[24, 24]} style={{ margin: "20px 0" }}>
                                    <Col span={24} style={{ margin: "auto" }}>
                                        <Button>
                                        <DownloadOutlined />  Download Resume
                                        </Button>
                                    </Col>
                                    <Col span={24}>
                                        <Title level={5}  > <FundProjectionScreenOutlined /> <span style={{ padding: "10px" }}>Timeline</span></Title>
                                        <Card>
                                            <Timeline mode="alternate" color="red" className="timeline-text" size={4}>
                                                <Timeline.Item color="green" className="timeline-text" dot={<CheckCircleOutlined />} >Shortlisted</Timeline.Item>
                                                <Timeline.Item color="gray" className="timeline-text">Round 1</Timeline.Item>
                                                <Timeline.Item color="blue" className="timeline-text">Round 2</Timeline.Item>
                                                <Timeline.Item color="blue" className="timeline-text" position="left">HR Round</Timeline.Item>
                                                <Timeline.Item dot={<ClockCircleOutlined className="timeline-clock-icon" />} color="red" className="timeline-text" >Hired</Timeline.Item>
                                            </Timeline>
                                        </Card>
                                    </Col> </Row>
                            </Col> */}
                        </Row>
                    </Spin>
                </Content>


            </Layout>




        </RootContext.Provider>)
    };
}

export default ViewCandidate;