// Packages
import { Tabs } from 'antd';
import { map } from 'lodash';
import feedback from '../../pages/feedback';

// View
import CandidateForm from './CandidateForm';
import CandidateLists from './CandidateLists';

import InterviewerForm from "./InterviewerForm";
import InterviewerLists from './InterviewerLists';

import ScheduleInterview from './ScheduleInterview';

import Visualization from "./Visualization";
const { TabPane } = Tabs;

// Tabs
const TABS = [
  
  {
    tab: "Candidate Dashboard",
    Component: CandidateLists
  },
  {
    tab: "Create Candidate",
    Component: CandidateForm
  },
  {
    tab: "Interviewer Actions",
    Component: InterviewerForm
  },
  {
    tab: "Feedback",
    Component: feedback
  }
  // {
  //   tab: "Schedule Interview",
  //   Component: ScheduleInterview
  // }
];

function Candidate() {
  return (
    <Tabs tabPosition="left">
      {
        map(TABS, ((TAB, index) => (
          <TabPane tab={TAB.tab} key={index}>
            <TAB.Component />
          </TabPane>
        )))
      }
    </Tabs>
  )
}

export default Candidate