import { useState } from 'react'
import { InfoCircleOutlined, LoadingOutlined } from "@ant-design/icons";
import { Button, Steps, Tooltip } from "antd";
const { Step } = Steps;
import { get, isEmpty } from "lodash";
import { react, useContext } from "react";
import { urls } from "../../../services/URLs";
import { RootContext } from "../../../Context/RootContext";

import { stepStatus, UpcomingStatusTitle, HistoryStatusTitle } from "../../../Constants/constants";
import { request } from "../../../services/request";


function ProgressBar(props) {
    const [currentInterviewerData, setCurrentInterviewerData] = useState('');
    const status = props.candidateFeedback?.status || ""
    const { candidateFeedback, role } = props
    const { candidateHistory } = props.candidateFeedback
    const count = candidateHistory && candidateHistory.length + 1 || 0;

    const currentstatus = get(stepStatus, status);
    const Title = get(UpcomingStatusTitle, status)

    const History = !isEmpty(candidateHistory) && candidateHistory.map((value, key) => {

        const feedback = () => {
            if (!isEmpty(value.feedBack)) {
                let string = " "
                for (const key in value.feedBack) {
                    string = string + "" + `${key} : ${value.feedBack[key]}`
                }
                return string
            }
        }

        const descriptionElement = (
            <code>
                {value.interviwerId && <> Interviewer : {value.interviwerId} </>}
                {value.feedBack && <><br />
                    <Tooltip placement="rightTop" title={feedback()}>Feedback  <InfoCircleOutlined />  </Tooltip>   </>}
                {value.overAllFeedBack && <> <br /> overAll Feedback : {value.overAllFeedBack} </>}
                {value.interviewDate && <> <br /> Interview Date : {value.interviewDate} </>}
                {value.interviewTime && <><br /> Interview Time : {value.interviewTime} </>}
                <br />
            </code>
        );
        const historystatus = get(HistoryStatusTitle, value.roundStatus)
        return (<Step
            key={key}
            title={historystatus}
            description={descriptionElement}
            status="finish"
        />)

    })

    const getCurrentInterviewerName = async (id) => {
        const finalUrl = `${get(urls[0], "INTERVIEWER")}/${id}`;
        const res = await request(finalUrl);
        setCurrentInterviewerData(res?.data?.interviewerName)
    } 

    const data = getCurrentInterviewerName(candidateFeedback.currentInterviewId)

    const currentStatus = (candidateFeedback, role) => {
        const feedback = () => {
            if (!isEmpty(candidateFeedback.feedBack)) {
                let string = " "
                for (const key in candidateFeedback.feedBack) {
                    string = string + "" + `${key} : ${candidateFeedback.feedBack[key]}`
                }
                return string
            }
        }

        const lastCandidateDescrption = (
            <code>
                {candidateFeedback.currentInterviewId && <> Interviewer : {currentInterviewerData} </>}

                {/* {<> <br />Feedback : {candidateFeedback.feedBack} </>} */}
                {candidateFeedback.feedBack && <Tooltip placement="rightTop" title={feedback()}>Feedback  <InfoCircleOutlined />  </Tooltip>}
                {candidateFeedback.overAllFeedBack && <> <br /> overAll Feedback : {candidateFeedback.overAllFeedBack} </>}
                {candidateFeedback.interviewDate && <> <br /> Interview Date : {candidateFeedback.interviewDate} </>}

                {candidateFeedback.interviewTime && <><br /> Interview Time : {candidateFeedback.interviewTime} </>}

                <br />
                <br />
                {
                    role === "admin" && (candidateFeedback.status === "ScheduleRound1" || candidateFeedback.status === "ScheduleRound2" || candidateFeedback.status === "ScheduledHr") ?
                        <Button onClick={() => props.rescheduleInterview()}>Reschedule</Button>
                        : null
                }
                <br />
            </code>
        );
        const historystatus = get(HistoryStatusTitle, candidateFeedback.status)
        return (
            candidateFeedback.status && <Step status={currentstatus} title={historystatus} description={lastCandidateDescrption}></Step>
        )

    }



    const nextStepsDescription = (
        <code>
            {candidateFeedback.nextInterviewDate && <> <br />Upcoming Interview Date : {candidateFeedback.nextInterviewDate} </>}

            {candidateFeedback.nextInterviewTime && <><br />Upcoming Interview Time : {candidateFeedback.nextInterviewTime} </>}
        </code>
    );

    return (<div>


        <Steps direction="vertical" current={count} status="error">
            {History}
            {currentStatus(candidateFeedback, role)}
            {!isEmpty(candidateFeedback.nextSteps) && <Step status="In Progress" icon={<LoadingOutlined />} title={Title} description={nextStepsDescription}></Step>}
        </Steps>
    </div>);
}

export default ProgressBar;