// Packages
import { useState, useContext } from "react";
import { Route, Redirect } from "react-router-dom";

// View
import UnAuthorized from "../components/Authorization/403";

//Utils
import { getFromSession } from "../utils/storage";

// Context
import { RootContext } from "../Context/RootContext";

function PrivateRoute({ component: Component, exact, ...rest }) {
    const { isLogedIn } = useContext(RootContext);
    return (
      <Route
        exact={exact}
        {...rest}
        render={(props) =>
          ((getFromSession("user_loged_in") === "admin" &&
            window.location.href.includes("admin")) ||
          (getFromSession("user_loged_in") === "interviewer" &&
            window.location.href.includes("interviewer"))) ? (
            <Component {...props} />
          ) : (
            <UnAuthorized />
          )
        }
      />
    );
}
export default PrivateRoute;
