// Packages

import { useContext ,useState ,useEffect} from 'react';
import {
    Form,
    Input,
    Select,
    Button,
    message,
    Spin,
    Checkbox
} from 'antd';

// Styles
import styles from "../../styles/create_admin.module.css";

// Requests
import { request, requestPost } from "../../services/request";

// Context
import { RootContext } from "../../Context/RootContext";

// urls
import { urls } from '../../services/URLs';
import { get } from 'lodash';
import InterviewerLists from './InterviewerLists';
import EditInterviewer from '../Interviewer/EditInterviewer';

const { Option } = Select;

const formItemLayout = {
    labelCol: {
        xs: {
            span: 24,
        },
        sm: {
            span: 8,
        },
    },
    wrapperCol: {
        xs: {
            span: 24,
        },
        sm: {
            span: 16,
        },
    },
};

const tailFormItemLayout = {
    wrapperCol: {
        xs: {
            span: 24,
            offset: 0,
        },
        sm: {
            span: 16,
            offset: 8,
        },
    },
};


function InterviewerForm() {

    const [form] = Form.useForm();
    const {setInterviewerRecord} = useContext(RootContext)
    const [isload,setisload] = useState(false)
    const [isCheckedSelector, setIsCheckedSelector] = useState(false);
    const [isCheckedHr, setIsCheckedHr] = useState(false);
    const [isCheckedAdmin, setIsCheckedAdmin] = useState(false);
    const [isEdit, setIsEdit] = useState(false);
    const [getAllInterviewerRecord, setGetAllInterviewerRecord] = useState([]);
    const [dataBaseId, setDataBaseId] = useState('');

    useEffect(async ()=>{
        const res = await request(get(urls[0], "INTERVIEWER"))
        setGetAllInterviewerRecord(res.data)
    }, [])

    const callInterviewerWhenUpdate = async () => {
        const res = await request(get(urls[0], "INTERVIEWER"))
        setGetAllInterviewerRecord(res.data)
    }

    const onChange = (e, type) => {
        type === "selector" && setIsCheckedSelector(e.target.checked);
        type === "hr" && setIsCheckedHr(e.target.checked);
        type === "admin" && setIsCheckedAdmin(e.target.checked);
    }

    const onFinish = async (values) => {
        const payload = {
            ...values,
            selector: isCheckedSelector,
            hr: isCheckedHr,
            admin: isCheckedAdmin
        }
        setisload(true)
        try {
            // message.loading('Action in progress..');
            setInterviewerRecord(await requestPost(get(urls[0], "INTERVIEWER"), payload))
            message.success('Interviewer created sucessfully');
            form.resetFields();
            setIsCheckedSelector(false)
            setIsCheckedHr(false)
            setIsCheckedAdmin(false)
            setisload(false)
            callInterviewerWhenUpdate()
        } catch (error) {
            message.error('Failed to create inteviwer');
            console.log(error);
            setisload(false)
            callInterviewerWhenUpdate()
        } 
        console.log('Received values of form: ', values);
    };

    useEffect(()=>{
        console.log("interviewer... mount") 
 
        return()=>{
            console.log("interviewer.. Unmount")
        }
     } )

    return (
      <Spin spinning={isload}>
        {!isEdit ? (
          <Form
            {...formItemLayout}
            form={form}
            name="create_interviewer_form"
            onFinish={onFinish}
            initialValues={{
              prefix: "91",
            }}
            scrollToFirstError
            className={styles.create_admin_form}
          >
            {/* <Form.Item
                name="companyId"
                label="Company Id"
                tooltip="DigiSprint Id "
                rules={[
                    {
                        required: true,
                        message: 'Please Enter Interviewer Id !',
                        whitespace: true,
                    },
                ]}
            >
                <Input />
            </Form.Item> */}

            {/* <Form.Item
                name="interviewerDesignation"
                label="Interviewer Designation"
                tooltip="Name as per certificate"
                rules={[
                    {
                        required: true,
                        message: 'Please Enter Interviewer Designation!',
                        whitespace: true,
                    },
                ]}
            >
                <Input />
            </Form.Item> */}

            {/* <Form.Item
                name="interviewerName"
                label="Interviewer Name"
                tooltip="Name as per certificate"
                rules={[
                    {
                        required: true,
                        message: 'Please Enter Interviewer Name!',
                        whitespace: true,
                    },
                ]}
            >
                <Input />
            </Form.Item> */}

            <Form.Item
              name="interviewerEmail"
              label="Interviewer E-mail"
              rules={[
                {
                  type: "email",
                  message: "The input is not valid E-mail!",
                },
                {
                  required: true,
                  message: "Please input your E-mail!",
                },
              ]}
            >
              <Input autoComplete="new-name" />
            </Form.Item>

            {/* <Form.Item
                name="interviewerPassword"
                label="password"
                rules={[
                    {
                        required: true,
                        message: 'Please input your password!',
                    },
                ]}
            >
                <Input.Password />
            </Form.Item> */}

            <Form.Item name="selector" label="Can Select">
              <Checkbox
                onChange={(e) => onChange(e, "selector")}
                checked={isCheckedSelector}
              ></Checkbox>
            </Form.Item>
            <Form.Item name="hr" label="HR">
              <Checkbox
                onChange={(e) => onChange(e, "hr")}
                checked={isCheckedHr}
              ></Checkbox>
            </Form.Item>
            <Form.Item name="admin" label="Admin">
              <Checkbox
                onChange={(e) => onChange(e, "admin")}
                checked={isCheckedAdmin}
              ></Checkbox>
            </Form.Item>

            <Form.Item {...tailFormItemLayout}>
              <Button type="primary" htmlType="submit">
                Create Interviewer
              </Button>
            </Form.Item>
          </Form>
        ) : (
          <EditInterviewer
            id={dataBaseId}
            callInterviewerWhenUpdate={callInterviewerWhenUpdate}
            setIsEdit={setIsEdit}
          />
        )}
        <InterviewerLists
          setIsEdit={setIsEdit}
          getAllInterviewerRecord={getAllInterviewerRecord}
          setDataBaseId={setDataBaseId}
        />
      </Spin>
    );
}

export default InterviewerForm;
