// Packages
import { useState ,useEffect, useContext } from 'react';
import {
  Table,
  Input,
  Popconfirm,
  Form,
  Typography,
  Select
} from 'antd';
import { Link } from 'react-router-dom';
import { request } from '../../../src/services/request'
import { urls } from '../../../src/services/URLs';
import { get } from 'lodash'; 
import { RootContext } from '../../Context/RootContext';
import { EyeOutlined } from '@ant-design/icons';

const { Option } = Select;
const originData = [];

function handleChange(value) {
  console.log(`selected ${value}`);
}

const EditableCell = ({
  editing,
  dataIndex,
  title,
  inputType,
  record,
  index,
  children,
  ...restProps
}) => {
  const inputNode = inputType === 'select' ? (
    <Select style={{ width: 120 }} onChange={handleChange}>
      <Option value="Selected">Selected</Option>
      <Option value="Rejected">Rejected</Option>
      <Option value="Pending">Pending</Option>
    </Select>)
    : <Input />;
  return (
    <td {...restProps}>
      {editing ? (
        <Form.Item
          name={dataIndex}
          style={{
            margin: 0,
          }}
          rules={[
            {
              required: true,
              message: `Please Input ${title}!`,
            },
          ]}
        >
          {inputNode}
        </Form.Item>
      ) : (
        children
      )}
    </td>
  );
};

const CandidateLists = () => {
  const [form] = Form.useForm();
  const [data, setData] = useState(originData);
  const [editingKey, setEditingKey] = useState('');
  const [candidates,setCandidates] = useState([]);
  const { interviewerDataWhenLogin } = useContext(RootContext);
  const [sessionId, setSessionId] = useState({});

  const isEditing = (record) => record.key === editingKey;

  useEffect(async () => { 
    const interviewerId = await (interviewerDataWhenLogin.id || sessionStorage.getItem("user_id"));
    const finalUrl = `${get(urls[0], "GET_CANDIDATES")}/${get(urls[0], "INTERVIEWER")}/${interviewerId}`;
    const res = await request(finalUrl)
      setCandidates(res.data)
  }, []);

  useEffect(async ()=>{
    const interviewerId = await (interviewerDataWhenLogin.id || sessionStorage.getItem("user_id"));
    const finalUrl = `${get(urls[0], "GET_CANDIDATES")}/${get(urls[0], "INTERVIEWER")}/${interviewerId}`;
    const res = await request(finalUrl)
      setCandidates(res.data)
  }, [sessionId])

  useEffect(()=>{
    if(interviewerDataWhenLogin.id || sessionStorage.getItem("user_id")){
      setSessionId(interviewerDataWhenLogin.id || sessionStorage.getItem("user_id"));
    }
  })


  const edit = (record) => {
    form.setFieldsValue({
      candidate_name: "",
      candidate_email: "",
      job_title: "",
      interview_slot: "",
      interview_link: "",
      PrevFeedback: "",
      status: "",
      ...record,
    });
    setEditingKey(record.key);
  };

  const cancel = () => {
    setEditingKey('');
  };

  const save = async (key) => {
    try {
      const row = await form.validateFields();
      const newData = [...data];
      const index = newData.findIndex((item) => key === item.key);

      if (index > -1) {
        const item = newData[index];
        newData.splice(index, 1, { ...item, ...row });
        setData(newData);
        setEditingKey('');
      } else {
        newData.push(row);
        setData(newData);
        setEditingKey('');
      }
      console.log("form", row, index);
    } catch (errInfo) {
      console.log('Validate Failed:', errInfo);
    }
  };

  // const handleDelete = async (key) => {
  //   try {
  //     const row = await form.validateFields();
  //     const newData = [...data];
  //     const index = newData.findIndex((item) => key.key === item.key);
  //     newData.splice(index, 1);
  //     setData(newData);

  //   } catch (errInfo) {
  //     console.log('Validate Failed:', errInfo);
  //   }
  // };

  const columns = [
    {
      title: 'Candidate Name',
      dataIndex: 'candidate_name',
      width: '15%',
      editable: true,
      render: (text, record) => (
        <Link to={`interviewer/viewcandidate/${record.id}`}>
        <span>{record.firstName} {record.lastName}</span>
          <span style={{float:"right"}}><EyeOutlined /></span>  
        </Link>),
    },
    {
      title: 'Candidate Email',
      dataIndex: 'email',
      width: '15%',
      editable: true,
    },
    {
      title: 'Job Title',
      dataIndex: 'jobTitle',
      width: '10%',
      editable: true,
    },
    {
      title: 'Interview Slot',
      dataIndex: 'interview_slot',
      width: '10%',
      editable: true,
    },
    {
      title: 'Interview link',
      dataIndex: 'interview_link',
      width: '15%',
      editable: true,
    },
    {
      title: 'Status',
      dataIndex: 'status',
      width: '20%',
      editable: true,
      filterMode: 'menu',
      filters: [
        {
          text: 'Pending',
          value: 'INPROGRESS',
        },
        {
          text: 'View All',
          value: '',
        }
      ],
      onFilter: (value, record) => record.candidateFeedback?.status.includes(value),
      render: (value, record) => (
        <span>{record.candidateFeedback?.status}</span>
      )
    },
      
    // {
    //   title: 'Add Feedback',
    //   dataIndex: 'AddFeedback',
    //   width: '20%',
    //   editable: true,
    //   render: (text, record) => (
    //     <Link to={`interviewer/Addfeedback_candidate/${record.id}`}>
    //       <span>Add Feedback</span></Link>),
    // },
    // {
    //   title: 'Action',
    //   dataIndex: 'Action',
    //   render: (_, record) => {
    //     const editable = isEditing(record);
    //     return editable ? (
    //       <span>
    //         <Typography.Link
    //           onClick={() => save(record.key)}
    //           style={{
    //             marginRight: 8,
    //           }}
    //         >
    //           Save
    //         </Typography.Link>
    //         <Popconfirm title="Sure to cancel?" onConfirm={cancel}>
    //           <a>Cancel</a>
    //         </Popconfirm>
    //       </span>
    //     ) : (
    //       <>
    //         <Typography.Link disabled={editingKey !== ''} onClick={() => edit(record)}>
    //           Edit
    //         </Typography.Link>
    //         {/* <Typography.Link disabled={editingKey !== ''} onClick={() => handleDelete(record)}>
    //           Delete
    //         </Typography.Link> */}
    //       </>
    //     );
    //   },
    // },
  ];

  const mergedColumns = columns.map((col) => {
    if (!col.editable) {
      return col;
    }

    return {
      ...col,
      onCell: (record) => ({
        record,
        inputType: col.dataIndex === 'status' ? 'select' : 'text',
        dataIndex: col.dataIndex,
        title: col.title,
        editing: isEditing(record),
      }),
    };
  });

  return (
    <Form form={form} component={false}>
      <Table
        components={{
          body: {
            cell: EditableCell,
          },
        }}
        bordered
        dataSource={candidates}
        columns={mergedColumns}
        rowClassName="editable-row"
        
        pagination={{
          onChange: cancel,
        }}
      />
    </Form>
  );
};

export default CandidateLists;