import { react, Component } from "react";
import { get } from "lodash";
import ProgressBar from "./Progressbar";
import { Button, Col, message, Modal, Row } from "antd";
import { urls } from "../../../services/URLs";
import { requestPost, request } from "../../../services/request";
import ScheduleInterview from "./ScheduleInteview";
import CandidateFeedback from "./AddFeedback";


class Candidateworkflow extends Component {
    constructor(props) {
        super(props);
        this.state = {
            showInterviewModal: false,
            showFeedbackModal: false,
            showRescheduleInterviewModal: false,
            interviewStartTime: null,
            interviewDate: null,
            interviewDuration: "30",
            candidate: {},
            interViewerId: null,
            feedbackComments: "",
            RoundStatus: null,
            interViewerList: {},
            hrList: {},
            carboncopyto: null,
            criteriaGroup: "",
            toggleHrModal: false,
        };
    }

    componentDidMount() {

        this.getinterviewlist()
        this.getHrList();

    }

    getinterviewlist = async () => {
        const finalurl = get(urls[0], "INTERVIEWER")
        const res = await request(finalurl)
        this.setState({
            interViewerList: res.data
        })
    }

    getHrList = async () => {
        const finalUrl = `${get(urls[0], "INTERVIEWER")}/hrs`;
        const res = await request(finalUrl)
        this.setState({
            hrList: res.data
        })
    }

    CandidateShortlist = async (id, action) => {
        try {
            const result = confirm("Are you sure?");

            if (result) {
                const payload = {
                    "action": action,
                    "valueMap": {
                        "candidateId": id
                    }

                }
                const finalurl = get(urls[1], "WORK_FLOW")
                const res = await requestPost(finalurl, payload, "post")
                this.props.refreshtheCandidateData()
            }

        }
        catch (ex) {
            console.error(ex)
        }



    }

    OpenshowRescheduleInterviewModal = () => {
        this.setState({
            showRescheduleInterviewModal: true
        })
    }
    OpenScheduleInterviewModal = () => {
        this.setState({
            showInterviewModal: true
        })
    }
    OpenFeedbackModal = () => {
        this.setState({
            showFeedbackModal: true
        })
    }
    showHrModal = () => {
        this.setState({
            toggleHrModal: true 
        })
    }
    handleOkFeedback = () => {
        this.setState({
            showFeedbackModal: false
        })
    }
    handleOkReschedule = () => {
        this.setState({
            showRescheduleInterviewModal: false
        })
    }

    handleOk = () => {
        this.setState({
            showInterviewModal: false,
            toggleHrModal: false 
        })
    }
    OnRescheduleInterview = () => {
        this.OpenshowRescheduleInterviewModal()
    }



    progressButton = (status, Action, id, role, isMatchedInterviewerId, enableFeedbackButtons) => {
        const isHr = sessionStorage.getItem("isHr") || false;
        const isSelector = sessionStorage.getItem("selector") || false
        switch (status) {
            case "Applied":
                return ((isMatchedInterviewerId && isSelector) && <><Button onClick={() => this.CandidateShortlist(id, "ShortListed")}>Shortlist</Button> <Button onClick={() => this.CandidateShortlist(id, "NotShortListed")}>Reject</Button></>)
            case "ShortListed":
                return (role === "admin" && <Button onClick={() => { this.OpenScheduleInterviewModal() }}>Schedule Round 1</Button>)
            case "NotShortListed":
                return ("")
            case "ScheduleRound1":
                return (((isMatchedInterviewerId && role === "interviewer") || (isMatchedInterviewerId && role === "admin")) && <Button disabled={!enableFeedbackButtons} onClick={() => { this.OpenFeedbackModal() }}>Add Feedback</Button>)
            case "RescheduleRound1":
                return (((isMatchedInterviewerId && role === "interviewer") || (isMatchedInterviewerId && role === "admin")) && <Button disabled={!enableFeedbackButtons} onClick={() => { this.OpenFeedbackModal() }}>Add Feedback</Button>)
            case "SelectedRound1":
                return ( role === "admin" && <Button onClick={() => { this.OpenScheduleInterviewModal() }}>Schedule Round 2</Button>)
            case "NotSelectedRound1":
                return ("")
            case "ScheduleRound2":
                return (((isMatchedInterviewerId && role === "interviewer") || (isMatchedInterviewerId && role === "admin")) && <><Button disabled={!enableFeedbackButtons} onClick={() => { this.OpenFeedbackModal() }}>Add Feedback</Button></>)
            case "RescheduleRound2":
                return (((isMatchedInterviewerId && role === "interviewer") || (isMatchedInterviewerId && role === "admin")) && <><Button disabled={!enableFeedbackButtons} onClick={() => { this.OpenFeedbackModal() }}>Add Feedback</Button></>)
            case "SelectedRound2":
                return (role === "admin" && <><Button onClick={() => { this.showHrModal() }}>Schedule HR</Button></>)
            case "ScheduledHr":
                return ((isMatchedInterviewerId && isHr) && <><Button disabled={!enableFeedbackButtons} onClick={() => { this.OpenFeedbackModal() }}>Add Feedback</Button></>)
            case "RescheduledHr":
                return ((isMatchedInterviewerId && isHr) && <><Button disabled={!enableFeedbackButtons} onClick={() => { this.OpenFeedbackModal() }}>Add Feedback</Button></>)
            case "Offered":
                return (isHr && <><Button onClick={() => { this.acceptOrRejectOffer(Action[1]) }}>Accepted offer</Button><Button onClick={() => { this.acceptOrRejectOffer(Action[0]) }}>Rejected offer</Button></>)
            default:
                return ("")
        }
    }

    ReScheduleInterview = async (status) => {
        let payloadaction = "";
        if (status === "ScheduleRound1") {
            payloadaction = "RescheduleRound1"
        }
        if (status === "ScheduleRound2") {
            payloadaction = "RescheduleRound2"
        }
        if (status === "ScheduledHr") {
            payloadaction = "RescheduleHr"
        }

        try {
            const action = payloadaction
            const payload = {
                "action": action,
                "valueMap": {
                    "candidateId": this.props.candidate.id,
                    "interviewerId": this.state.interViewerId,
                    "interviewDate": this.state.interviewDate,
                    "interviewTime": this.state.interviewStartTime,
                    "timeTakenForInterview": Number(this.state.interviewDuration),
                    "criteriaGroup": this.state.criteriaGroup
                }
            }
            const finalurl = get(urls[1], "WORK_FLOW")
            const res = await requestPost(finalurl, payload, "post")
            if(res.status === 200){
                this.props.setLoader(false)
                this.handleOk()
                this.handleOkReschedule()
            }
            this.setState({
                showInterviewModal: false
            })
            this.props.refreshtheCandidateData()


        }
        catch (ex) {
            message.error(ex)
            console.error(ex)
        }
    }

    ScheduleInterview = async (Nextaction) => {
        try {
            const action = Nextaction[0]
            const payload = {
                "action": action,
                "valueMap": {
                    "candidateId": this.props.candidate.id,
                    "interviewerId": this.state.interViewerId,
                    "interviewDate": this.state.interviewDate,
                    "interviewTime": this.state.interviewStartTime,
                    "timeTakenForInterview": Number(this.state.interviewDuration),
                    "criteriaGroup": this.state.criteriaGroup
                }
            }

            const finalurl = get(urls[1], "WORK_FLOW")
            const res = await requestPost(finalurl, payload, "post")
            if(res.status === 200){
                this.props.setLoader(false)
                this.handleOk()
                this.handleOkReschedule()
            }
            this.setState({
                showRescheduleInterviewModal: false
            })
            this.props.refreshtheCandidateData()


        }
        catch (ex) {
            message.error(ex)
            console.error(ex)
        }
    }

    OnSubmitFeedack = async (val) => {
        try {
            const action = this.state.RoundStatus
            const payload = {
                "action": action,
                "valueMap": {
                    "candidateId": this.props.candidate.id,
                    "feedBack": val,
                    "overAllFeedBack": this.state.feedbackComments
                }

            }
            const finalurl = get(urls[1], "WORK_FLOW")
            const res = await requestPost(finalurl, payload, "post")
            this.setState({
                showFeedbackModal: false
            })
            this.props.refreshtheCandidateData()
        }
        catch (ex) {
            console.error(ex)
        }
    }

    acceptOrRejectOffer = async (nextaction) => {
        try {
            const action = nextaction
            const payload = {
                "action": action,
                "valueMap": {
                    "candidateId": this.props.candidate.id
                }
            }
            const finalurl = get(urls[1], "WORK_FLOW")
            const res = await requestPost(finalurl, payload, "post")
            this.props.refreshtheCandidateData()

        }
        catch (ex) {
            message.error(ex)
            console.error(ex)
        }
    }


    interviewrChange = (e) => {
        this.setState({
            interViewerId: e
        })
    }
    interviewTimeChange = (time, timeString) => {
        this.setState({
            interviewStartTime: timeString
        })
    }
    interviewDatechange = (date, dateString) => {
        this.setState({
            interviewDate: dateString
        })
    }
    interviewDurationChange = (value) => {
        this.setState({
            interviewDuration: value
        })
    }
    addCarbonCopyTo = (value) => {
        this.setState({
            carboncopyto: value
        })
    }

    addFeedbackCriteria = (value) => {
        this.setState({
            criteriaGroup: value
        })
    }

    OnChangeRoundtatus = (e) => {
        this.setState({
            RoundStatus: e.target.value
        })
    }
    OnchangeFeedbackComments = (e) => {
        this.setState({
            feedbackComments: e.target.value
        })
    }

    checkForInterviewer = (id) => {
        const existingId = sessionStorage.getItem("user_id");
        if(existingId === id){
            return true;
        }
        return false;
    }

    checkCurrentDateAndTime = (nextInterviewDate ,nextInterviewTime) => {
        const timeStamp = `${nextInterviewDate} ${nextInterviewTime}`;
        const date = new Date();
        const interviewDate = new Date(timeStamp);
        return date >= interviewDate
    }

    render() {
        const candidate = get(this.props, "candidate");
        const candidateFeedback = get(candidate, "candidateFeedback")
        const actionValues = get(candidateFeedback, "nextSteps")
        // const { role } = this.props;
        const role = sessionStorage.getItem("user_loged_in")
        const { status, currentInterviewId, nextInterviewDate ,nextInterviewTime } = candidateFeedback;
        const isMatchedInterviewerId = this.checkForInterviewer(currentInterviewId)
        const enableFeedbackButtons = this.checkCurrentDateAndTime(nextInterviewDate ,nextInterviewTime)

        return (<>
            <Row gutter={[24, 24]} style={{ width: "100%" }}>
                <Col span={24}>
                    {candidate && <ProgressBar
                        rescheduleInterview={() => this.OnRescheduleInterview()}
                        candidateFeedback={candidateFeedback}
                        role={role} />}
                </Col>
                <Col span={24}>
                    {this.progressButton(candidateFeedback?.status, actionValues, candidate?.id, role, isMatchedInterviewerId, enableFeedbackButtons)}
                </Col >
            </Row>

            <div>
                <Modal visible={this.state.showInterviewModal} onCancel={() => { this.handleOk() }}
                    footer={[
                        <Button key="back" onClick={() => this.handleOk()}>
                            cancel
                        </Button>]}>
                    <ScheduleInterview
                        interViewerList={this.state.interViewerList}
                        onInterviewrChange={(e) => { this.interviewrChange(e) }}
                        onInterviewTimeChange={(time, timeString) => { this.interviewTimeChange(time, timeString) }}
                        onInterviewDateChange={(date, dateString) => { this.interviewDatechange(date, dateString) }}
                        onInterviewDurationChange={(e) => { this.interviewDurationChange(e) }}
                        onSubmitScheduleInterview={() => { this.ScheduleInterview(actionValues) }}
                        OnSelectCC={(e) => { this.addCarbonCopyTo(e) }}
                        OnSelectFeedbackCriteria={(e) => { this.addFeedbackCriteria(e) }}
                        previousData={candidateFeedback}
                    />
                </Modal>
                <Modal visible={this.state.toggleHrModal} onCancel={() => { this.handleOk() }}
                    footer={[
                        <Button key="back" onClick={() => this.handleOk()}>
                            cancel
                        </Button>]}>
                    <ScheduleInterview
                        interViewerList={this.state.hrList}
                        onInterviewrChange={(e) => { this.interviewrChange(e) }}
                        onInterviewTimeChange={(time, timeString) => { this.interviewTimeChange(time, timeString) }}
                        onInterviewDateChange={(date, dateString) => { this.interviewDatechange(date, dateString) }}
                        onInterviewDurationChange={(e) => { this.interviewDurationChange(e) }}
                        onSubmitScheduleInterview={() => { this.ScheduleInterview(actionValues) }}
                        OnSelectCC={(e) => { this.addCarbonCopyTo(e) }}
                        OnSelectFeedbackCriteria={(e) => { this.addFeedbackCriteria(e) }}
                        previousData={candidateFeedback}
                    />
                </Modal>
                <Modal visible={this.state.showRescheduleInterviewModal} onCancel={() => { this.handleOkReschedule() }}
                    footer={[
                        <Button key="back" onClick={() => this.handleOkReschedule()}>
                            cancel
                        </Button>]}>
                    <ScheduleInterview
                        interViewerList={this.state.interViewerList}
                        onInterviewrChange={(e) => { this.interviewrChange(e) }}
                        onInterviewTimeChange={(time, timeString) => { this.interviewTimeChange(time, timeString) }}
                        onInterviewDateChange={(date, dateString) => { this.interviewDatechange(date, dateString) }}
                        onInterviewDurationChange={(e) => { this.interviewDurationChange(e) }}
                        onSubmitScheduleInterview={() => { this.ReScheduleInterview(status) }}
                        OnSelectCC={(e) => { this.addCarbonCopyTo(e) }}
                        OnSelectFeedbackCriteria={(e) => { this.addFeedbackCriteria(e) }}
                        previousData={candidateFeedback}
                        RescheduleFlag={true}
                    />
                </Modal>
                <Modal visible={this.state.showFeedbackModal} onCancel={() => { this.handleOkFeedback() }}
                    footer={[
                        <Button key="back" onClick={() => this.handleOkFeedback()}>
                            cancel
                        </Button>]}>

                    <CandidateFeedback
                        OnChangeRoundtatus={(e) => { this.OnChangeRoundtatus(e) }}
                        NextactionValues={actionValues}
                        OnchangeFeedbackComments={(e) => { this.OnchangeFeedbackComments(e) }}
                        OnSubmitFeedack={(val) => { this.OnSubmitFeedack(val) }}
                        criteriaGroup={candidateFeedback.criteriaGroup} />


                </Modal>

            </div>




        </>);
    }
}

export default Candidateworkflow;