// Packages
import { useEffect,useState } from 'react';
import {
    Form,
    Input,
    Select,
    Button,
    Row,
    Col,
    message,
    Checkbox
} from 'antd';
import {
    ArrowLeftOutlined,
} from '@ant-design/icons';
import { Link } from 'react-router-dom';

// Styles
import styles from "../../styles/create_admin.module.css";
import { createRequest, request } from '../../services/request';
import { get,isEmpty } from 'lodash';
import { urls } from '../../services/URLs';

const { Option } = Select;

const formItemLayout = {
    labelCol: {
        xs: {
            span: 24,
        },
        sm: {
            span: 8,
        },
    },
    wrapperCol: {
        xs: {
            span: 24,
        },
        sm: {
            span: 16,
        },
    },
};

const tailFormItemLayout = {
    wrapperCol: {
        xs: {
            span: 24,
            offset: 0,
        },
        sm: {
            span: 16,
            offset: 8,
        },
    },
};

function EditInterviewer({ id, record, callInterviewerWhenUpdate, setIsEdit }) {
    const [form] = Form.useForm();
    const [initData, setinitData] = useState();
    const [isCheckedSelector, setIsCheckedSelector] = useState(false);
    const [isCheckedHr, setIsCheckedHr] = useState(false);
    const [isCheckedAdmin, setIsCheckedAdmin] = useState(false);

    const onFinish = async (values) => {
        const payload = {
            ...values,
            id,
            companyId:values.companyId,
            selector: isCheckedSelector,
            hr: isCheckedHr,
            admin: isCheckedAdmin
        }
        try {
            message.loading('Action in progress..');
            const response = await createRequest(get(urls[0], "INTERVIEWER"), 'put', payload )
            if(response){
                message.success('this is a success message')
                callInterviewerWhenUpdate()
                setIsEdit(false)
            }
        } catch (error) {
            message.error('This is an error message');
            console.log(error);
        } 
        console.log('Received values of form: ', values);
        
    };

    const setInitialSelect = (type, data) => {
        type === "selector" && setIsCheckedSelector(data);
        type === "hr" && setIsCheckedHr(data);
        type === "admin" && setIsCheckedAdmin(data);
    }

    const onChange = (e, type) => {
        type === "selector" && setIsCheckedSelector(e.target.checked);
        type === "hr" && setIsCheckedHr(e.target.checked);
        type === "admin" && setIsCheckedAdmin(e.target.checked);
     }

    useEffect(() => {
        request(get(urls[0], "INTERVIEWER") + `/${id}`).then((res) => {
            setinitData(res.data)
            setinitData((state) => {
                form.setFieldsValue({
                    ...state
                })
            })
            setInitialSelect("selector", res.data.selector)
            setInitialSelect("hr", res.data.hr)
            setInitialSelect("admin", res.data.admin)
        }, [])
    }, [])

    useEffect(() => {
        request(get(urls[0], "INTERVIEWER") + `/${id}`).then((res) => {
            setinitData(res.data)
            setinitData((state) => {
                form.setFieldsValue({
                    ...state
                })
            })
            setInitialSelect("selector", res.data.selector)
            setInitialSelect("hr", res.data.hr)
            setInitialSelect("admin", res.data.admin)
        }, [])
    }, [id])

    return (
      <>
        <Form
          {...formItemLayout}
          form={form}
          name="create_interviewer_form"
          onFinish={onFinish}
          initialValues={{
            interviewerId: record?.id,
            interviewerDesignation: record?.interviewerDesignation,
            interviewerName: record?.interviewerName,
            interviewerEmail: record?.interviewerEmail,
          }}
          scrollToFirstError
          className={styles.create_admin_form}
        >
          <ArrowLeftOutlined
            style={{ fontSize: "20px", color: "#08c", cursor: "pointer" }}
            onClick={() => {
              setIsEdit(false);
            }}
          />
          {/* <Form.Item
                    name="interviewerDesignation"
                    label="Interviewer Designation"
                    tooltip="Name as per certificate"
                    rules={[]}
                >
                    <Input />
                </Form.Item>

                <Form.Item
                    name="interviewerName"
                    label="Interviewer Name"
                    tooltip="Name as per certificate"
                    rules={[]}
                >
                    <Input />
                </Form.Item> */}

          <Form.Item
            name="interviewerEmail"
            label="Interviewer E-mail"
            rules={[]}
          >
            <Input />
          </Form.Item>

          {/* <Form.Item
                    name="interviewerPassword"
                    label="password"
                    rules={[
                        {
                            required: true,
                            message: 'Please input your password!',
                        },
                    ]}
                >
                    <Input.Password />
                </Form.Item>

                <Form.Item
                    name="companyId"
                    label="Company Id"
                    tooltip="DigiSprint Id "
                    rules={[]}
                >
                    <Input />
                </Form.Item> */}

          <Form.Item name="selector" label="Can Select">
            <Checkbox
              onChange={(e) => onChange(e, "selector")}
              checked={isCheckedSelector}
            ></Checkbox>
          </Form.Item>

          <Form.Item name="hr" label="HR">
            <Checkbox
              onChange={(e) => onChange(e, "hr")}
              checked={isCheckedHr}
            ></Checkbox>
          </Form.Item>

          <Form.Item name="admin" label="Admin">
            <Checkbox
              onChange={(e) => onChange(e, "admin")}
              checked={isCheckedAdmin}
            ></Checkbox>
          </Form.Item>

          <Form.Item {...tailFormItemLayout}>
            <Button type="primary" htmlType="submit">
              Edit Interviewer
            </Button>
          </Form.Item>
        </Form>
      </>
    );
}

export default EditInterviewer