// Packages
import React, { useState, useEffect } from 'react';
import {
    Form,
    Input,
    Select,
    Button,
    DatePicker,
    TimePicker
} from 'antd';
import { request, requestPost } from '../../../services/request';
import { get, isEmpty } from 'lodash';
import { urls } from '../../../services/URLs';
const { RangePicker } = TimePicker;
import moment from 'moment';
// Styles
// import styles from "../../styles/create_admin.module.css";

const { Option } = Select;

const formItemLayout = {
    labelCol: {
        xs: {
            span: 24,
        },
        sm: {
            span: 8,
        },
    },
    wrapperCol: {
        xs: {
            span: 24,
        },
        sm: {
            span: 16,
        },
    },
};

const tailFormItemLayout = {
    wrapperCol: {
        xs: {
            span: 24,
            offset: 0,
        },
        sm: {
            span: 16,
            offset: 8,
        },
    },
};

function ScheduleInterview(props) {
    const [form] = Form.useForm();
    const [interviewDate, setInterviewDate] = useState();
    const [interviewTime, setInterviewTime] = useState([]);
    const [interviewerList, setinterviewerList] = useState({})
    const [criteriaGroup, setcriteriaGroup] = useState(null)

    useEffect(() => {
        const preInfo = props.previousData;
        const PreviousInterviewData = {
            "interviewDate": preInfo.nextInterviewDate || "",
            "interviewTime": preInfo.nextInterviewTime || "",
            "intervieweDuration": preInfo.timeTakenForInterview || "",
            "interviewerId": preInfo.currentInterviewId || "",
            "FeedBackCriteria": preInfo.criteriaGroup || ""
        }
        if (props.RescheduleFlag) {
            props.onInterviewrChange(preInfo.currentInterviewId);
            props.onInterviewDateChange(moment(preInfo.nextInterviewDate), preInfo.nextInterviewDate);
            props.onInterviewTimeChange(moment(preInfo.nextInterviewTime), preInfo.nextInterviewTime);
            props.onInterviewDurationChange(preInfo.timeTakenForInterview);
            props.OnSelectFeedbackCriteria(preInfo.criteriaGroup);

            if (preInfo.nextInterviewDate) {
                form.setFieldsValue({
                    interviewDate: moment(PreviousInterviewData.interviewDate),
                    interviewTime: moment(PreviousInterviewData.nextInterviewTime),
                    interviewerName: PreviousInterviewData.interviewerId,
                    intervieweDuration: PreviousInterviewData.interviewDuration,
                    FeedBackCriteria: PreviousInterviewData.FeedBackCriteria
                });
            }
        }



    }, [])

    useEffect(async () => {
        const res = await request(get(urls[0], "CRITERIA_GROUP"))
        if (res.data) {
            setcriteriaGroup(res.data)
        }
    }, [])



    const onFinish = (values) => {
        props.onSubmitScheduleInterview()
    };
    const getInteviewerList = props?.interViewerList.map((value, key) => {
        return (
            <Option id={key} value={value.id}>{value.interviewerEmail}</Option>
        )
    })

    const getCriteriaGroup = !isEmpty(criteriaGroup) && criteriaGroup.map((value, key) => {
        return (
            <Option id={key} value={value.id}>{value.id}</Option>
        )
    })

    // function handleChange(value) {
    //     console.log(`selected ${value}`);
    // }

    // function onChange(date, dateString) {
    //     console.log(date, dateString);
    // }

    return (
        <Form
            {...formItemLayout}
            form={form}
            name="schedule_interview_form"
            onFinish={onFinish}
            scrollToFirstError
            style={{ width: "90%" }}
        >


            <Form.Item
                name="interviewerName"
                label="Interviewer Name"
                rules={[
                    {
                        required: true,
                        message: 'Please provide Interviewer Name !'
                    },
                ]}
            >
                {/* <Input onChange={(e)=>props.onInterviewrChange(e)} /> */}
                <Select placeholder="Please Select Interviewer" onChange={(e) => { props.onInterviewrChange(e) }} >
                    {getInteviewerList}
                </Select>
            </Form.Item>




            <Form.Item
                name="interviewDate"
                label="Interview Date"
                rules={[
                    {
                        required: true,
                        message: 'Please Enter Interview Date'
                    },
                ]}
            >
                <DatePicker
                    onChange={(date, dateString) => props.onInterviewDateChange(date, dateString)}
                    style={{ width: "100%" }}
                    disabledDate={(current) => {
                        return moment().add(-1, 'days') >= current;
                    }}

                />
            </Form.Item>

            <Form.Item
                name="interviewTime"
                label="Interview Start Time"
                rules={[
                    {
                        required: true,
                        message: 'Please Enter Interview Start Time'
                    },
                ]}
            >
                <TimePicker
                    onChange={(time, timeString) => props.onInterviewTimeChange(time, timeString)}
                    style={{ width: "100%" }}
                    minuteStep={15} secondStep={10} />
            </Form.Item>
            <Form.Item
                name="intervieweDuration"
                label="interviewe Duration"
                rules={[
                    {
                        required: true,
                        message: 'Please provide Interviewe Duration'
                    },
                ]}
            >
                <Select placeholder="Please select" onChange={(e) => { props.onInterviewDurationChange(e) }} >
                    <Option value="15">15 min</Option>
                    <Option value="30">30 min</Option>
                    <Option value="45">45 min</Option>
                    <Option value="60">60 min</Option>
                </Select>
            </Form.Item>

            {/* <Form.Item
                // name="intervieweDuration"
                label="Send Carbon Copy To"
                // rules={[
                //     {
                //         required: true,
                //         message: 'Please provide Interviewe Duration'
                //     },
                // ]}
            >
                <Select
                    mode="multiple"
                    allowClear
                    style={{ width: '100%' }}
                    placeholder="Please select"
                    // defaultValue={['a10', 'c12']}
                    onChange={(e)=>{ props.OnSelectCC(e)}}
                >
                    <Option value="15">Gopi Vemula</Option>
                    <Option value="30">Siva D</Option>
                    <Option value="45">Bakkesh</Option>
                    <Option value="60">Shunmuga</Option>
                </Select>

            </Form.Item> */}

            <Form.Item
                name="FeedBackCriteria"
                label="Feedback Criteria"
                rules={[
                    {
                        required: true,
                        message: 'Please select Feedback Criteria'
                    },
                ]}
            >
                <Select
                    allowClear
                    style={{ width: '100%' }}
                    placeholder="Please select"
                    onChange={(e) => { props.OnSelectFeedbackCriteria(e) }}
                >
                    {getCriteriaGroup}
                </Select>

            </Form.Item>




            <Form.Item {...tailFormItemLayout}>
                <Button type="primary" htmlType="submit">
                    Schedule Interview
                </Button>
            </Form.Item>
        </Form>
    )
}

export default ScheduleInterview