// Packages
import { useContext } from "react";

import { Link, withRouter, useLocation } from "react-router-dom";

import { Button, Image, Typography } from "antd";
import { AzureAD, AuthenticationState } from "react-aad-msal";
import { authProvider } from "../AdLogin/authProvider";
import styles from "../../styles/login.module.css";

// Context
import { RootContext } from "../../Context/RootContext";

// Style
import style from "../../styles/header.module.css";

// utils
import { getFromSession, removeFromSession } from "../../utils/storage";
import logo from "../../Assets/DigiSprint-Logo.png";
import { microsoftLoginPath } from "../../Constants/constants";

function Header() {
  const { Title } = Typography;
  // let location = useLocation();
  const { isLogedIn, setIsLogedIn } = useContext(RootContext);
  const microsoftToken = sessionStorage.getItem(
    "msal.ba6f6865-88a9-43b7-ac2b-b002b3168225.idtoken"
  );

  const logoutFromMicrosoft = () => {
    setIsLogedIn(false);
    removeFromSession("user_loged_in");
    removeFromSession("userName");
    sessionStorage.removeItem("id");
    sessionStorage.removeItem("userName");
    sessionStorage.clear();
  };

  return (
    <div className={style.header_wrapper}>
      <Image src={logo} preview={false} alt="logo" />

      <div className={style.header_btn_wrapper}>
        {(isLogedIn || getFromSession("user_loged_in")) && (
          <Title level={4}>{`Hello ${sessionStorage.getItem('userName')}`}</Title>
        )}
        {(microsoftToken && getFromSession("user_loged_in")) ? (
          <AzureAD provider={authProvider} forceLogin={false}>
            {({ accountInfo, authenticationState, error, login, logout }) => {
              return (
                <div>
                  {console.log(authenticationState)}
                  {console.log(accountInfo)}
                  <button className={styles.microsoft_logout} onClick={logout}>
                  <div onClick={()=>{logoutFromMicrosoft()}}>
                  <img
                      className={styles.microsoft_logo}
                      alt="svgImg"
                      src={microsoftLoginPath}
                    />{" "}
                    Sign out
                  </div>
                  </button>
                </div>
              );
            }}
          </AzureAD>
        ) : (
          // <Button
          //   href="/"
          //   className={style.header_btn_logout}
          //   shape="round"
          //   size="large"
          //   onClick={() => {
          //     logout();
          //   }}
          // >
          //   {isLogedIn || getFromSession("user_loged_in")
          //     ? "Log out"
          //     : "Log in"}
          // </Button>
          null
        )}
        {/* <Title level={5}>
          <Link to="/" onClick={() => { setIsLogedIn(false); removeFromSession("user_loged_in"); sessionStorage.removeItem("id") }}>{(isLogedIn || getFromSession("user_loged_in")) ? "Log out" : "Log in"}</Link>
        </Title> */}
      </div>
    </div>
  );
}

export default withRouter(Header);
