// Packages
import { useState, useContext, useEffect } from 'react';
import {
  Table,
  Input,
  InputNumber,
  Popconfirm,
  Form,
  Typography,
  Spin,
  message
} from 'antd';
import { DeleteOutlined, EditOutlined } from '@ant-design/icons';
import { Link } from 'react-router-dom';

// Requests
import { request, requestDelete, requestPost } from "../../services/request";

// Context
import { RootContext } from "../../Context/RootContext"

// urls
import { urls } from '../../services/URLs';
import { get, isEmpty, map } from 'lodash';

const EditableCell = ({
  editing,
  dataIndex,
  title,
  inputType,
  record,
  index,
  children,
  ...restProps
}) => {
  const inputNode = inputType === 'number' ? <InputNumber /> : <Input />;
  return (
    <td {...restProps}>
      {editing ? (
        <Form.Item
          name={dataIndex}
          style={{
            margin: 0,
          }}
          rules={[
            {
              required: true,
              message: `Please Input ${title}!`,
            },
          ]}
        >
          {inputNode}
        </Form.Item>
      ) : (
        children
      )}
    </td>
  );
};

const InterviewerLists = ({ setIsEdit, getAllInterviewerRecord, setDataBaseId }) => {
  const [form] = Form.useForm();
  const [editingKey, setEditingKey] = useState('');
  const [isload,setisload] = useState(false)
  const [interviewerAllData, setInterviewerAllData] = useState(getAllInterviewerRecord);
   
  useEffect(()=>{
    setInterviewerAllData(getAllInterviewerRecord)
  }, [])

  useEffect(()=>{
    setInterviewerAllData(getAllInterviewerRecord)
  }, [getAllInterviewerRecord])

  const isEditing = (record) => record.key === editingKey;

  const edit = (key) => {
    setIsEdit(true)
    setDataBaseId(key.id)
  };

  const cancel = () => {
    setEditingKey('');
  };

  const save = async (key) => {
    try {
      const row = await form.validateFields();
      const newData = [...data];
      const index = newData.findIndex((item) => key === item.key);

      if (index > -1) {
        const item = newData[index];
        newData.splice(index, 1, { ...item, ...row });
        setData(newData);
        setEditingKey('');
      } else {
        newData.push(row);
        setData(newData);
        setEditingKey('');
      }
      console.log("form", row, index);
    } catch (errInfo) {
      console.log('Validate Failed:', errInfo);
    }
  };

  const handleDelete = async (key) => {
    try {
      const result = confirm("Want to delete?");
      if (result) {
        setisload(true)
        const finalUrl = get(urls[0], "DELETE_INTERVIEWER") + key.id
        const res = await requestDelete(finalUrl, "delete");
        const row = await form.validateFields();
        const newData = [...interviewerAllData];
        const index = newData.findIndex((item) => key.id === item.id);
        newData.splice(index, 1);
        setInterviewerAllData(newData);
        setisload(false)
        message.success("Interviwer deleted succesfully.")
      }
    } catch (errInfo) {
      setisload(false)
      message.error("Failed to deleted Interviwer")
      console.log('Validate Failed:', errInfo);
    }
  };

  const columns = [
    // {
    //   title: 'Company Id',
    //   dataIndex: 'companyId',
    //   width: '10%',
    //   editable: true,
    // },
    {
      title: 'Interviewer Name',
      dataIndex: 'interviewerName',
      width: '15%',
      editable: true,
    },
    // {
    //   title: 'Interviewer Designation',
    //   dataIndex: 'interviewerDesignation',
    //   width: '20%',
    //   editable: true,
    // },
    {
      title: 'Interviewer Email',
      dataIndex: 'interviewerEmail',
      width: '25%',
      editable: true,
    },
    {
      title: 'Action',
      dataIndex: 'operation',
      render: (_, record) => {
        const editable = isEditing(record);
        return editable ? (
          <span>
            <Typography.Link
              onClick={() => save(record.key)}
              style={{
                marginRight: 8,
              }}
            >
              Save
            </Typography.Link>
            <Popconfirm title="Sure to cancel?" onConfirm={cancel}>
              <a>Cancel</a>
            </Popconfirm>
          </span>
        ) : (
          <>
            <Typography.Link disabled={editingKey !== ''} onClick={() => edit(record)}  style={{
                marginRight: 10,
              }}>
              <EditOutlined />
            </Typography.Link>
            <Typography.Link disabled={editingKey !== ''} onClick={() => handleDelete(record)} >
              <DeleteOutlined />
            </Typography.Link>
          </>
        );
      },
    },
  ];

  const mergedColumns = columns.map((col) => {
    if (!col.editable) {
      return col;
    }

    return {
      ...col,
      onCell: (record) => ({
        record,
        inputType: col.dataIndex === 'age' ? 'number' : 'text',
        dataIndex: col.dataIndex,
        title: col.title,
        editing: isEditing(record),
      }),
    };
  });

  return (
    <>
    {!isEmpty(interviewerAllData) &&
    <Form form={form} component={false}>
      <Spin spinning={isload}>
      <Table
        components={{
          body: {
            cell: EditableCell,
          },
        }}
        bordered
        dataSource={interviewerAllData}
        columns={mergedColumns}
        rowClassName="editable-row"
        pagination={{
          onChange: cancel,
        }}
      />
      </Spin>
      
    </Form>}
    </>
  );
};

export default InterviewerLists;