// Packages
import { useContext, useState, useEffect } from 'react';
import {
  Table,
  Input,
  Popconfirm,
  Form,
  Typography,
  Select,
  Spin,
  Alert,
  message
} from 'antd';
import { Link } from 'react-router-dom';
import { RootContext } from '../../Context/RootContext';
import { request, requestDelete, requestPost } from '../../services/request';
import { get, isEmpty } from 'lodash';
import { urls } from '../../services/URLs';
import { DeleteOutlined, EditOutlined, EyeOutlined } from '@ant-design/icons';

const { Option } = Select;
const originData = [];
function handleChange(value) {
  console.log(`selected ${value}`);
}

const EditableCell = ({
  editing,
  dataIndex,
  title,
  inputType,
  record,
  index,
  children,
  ...restProps
}) => {
  const inputNode = inputType === 'select' ? (
    <Select style={{ width: 120 }} onChange={handleChange}>
      <Option value="Selected">Selected</Option>
      <Option value="Rejected">Rejected</Option>
      <Option value="Pending">Pending</Option>
    </Select>)
    : <Input />;
  return (
    <td {...restProps}>
      {editing ? (
        <Form.Item
          name={dataIndex}
          style={{
            margin: 0,
          }}
          rules={[
            {
              required: true,
              message: `Please Input ${title} !`,
            },
          ]}
        >
          {inputNode}
        </Form.Item>
      ) : (
        children
      )}
    </td>
  );
};

const CandidateLists = () => {
  const {getAllCandidate} = useContext(RootContext);
  const [form] = Form.useForm();
  const [data, setData] = useState(originData);
  const [editingKey, setEditingKey] = useState('');
  const [candidates, setCandidates] = useState(getAllCandidate)
  const [isload,setisload] = useState(false)
  const isEditing = (record) => record.key === editingKey;

  useEffect(async () => {
    setisload(true)
    const candidatelist = await request(get(urls[0], "GET_CANDIDATES"))
    setCandidates(candidatelist.data)
    setisload(false)
    console.log("candidatelist",candidatelist)
    console.log("useeffect as component did mount")
    return () => {
      console.log("useeffect as component will un mount")
    }
  },[])

  useEffect(async () => {
    setisload(true);
    const candidatelist = await request(get(urls[0], "GET_CANDIDATES"));
    setCandidates(candidatelist.data);
    setisload(false);
  }, [getAllCandidate]);

  //   candidates.map( (lists, i) => { 
  //     console.log("list",lists)
  //     !isEmpty(lists) && originData.push({
  //       key: i.toString(),
  //       candidate_name: <Link to={`/admin/edit_interviewer/${get(lists,"id")}`}>{get(lists,"firstName") + get(lists,"lastName")}</Link>,
  //       job_title: get(lists,"jobTitle"),
  //       candidate_email: get(lists,"email"),
  //       status:'',
  //       interviewer_name:'',
  //       Feedback:''

  //     })
  //   });

  // console.log({data},originData)
  const edit = (record) => {
    form.setFieldsValue({
      candidate_name: "",
      candidate_email: "",
      job_title: "",
      interviewer_name: "",
      Feedback: "",
      status: "",
      ...record,
    });
    setEditingKey(record.key);
  };

  const cancel = () => {
    setEditingKey('');
  };

  const save = async (key) => {
    try {
      const row = await form.validateFields();
      const newData = [...data];
      const index = newData.findIndex((item) => key === item.key);

      if (index > -1) {
        const item = newData[index];
        newData.splice(index, 1, { ...item, ...row });
        setData(newData);
        setEditingKey('');
      } else {
        newData.push(row);
        setData(newData);
        setEditingKey('');
      }
      console.log("form", row, index);
    } catch (errInfo) {
      console.log('Validate Failed:', errInfo);
    }
  };

  const handleDelete = async (key) => {
    try {
      const result = confirm("Want to delete?");
      if (result) {
        setisload(true)
        const finalUrl = get(urls[0], "DELETE_CANDIDATE") + `${key.id}`;
        const res = await requestDelete(finalUrl, "delete");
        const candidatelist = await request(get(urls[0], "GET_CANDIDATES"))
        setCandidates(candidatelist.data)
        if (res) {
          setisload(false)
          message.success("Candidate deleted succefully.")

          //need to switch to candidate dashboard
        }
      }
    } catch (errInfo) {
      message.success("Failed to delete Candidate")
      console.log('Validate Failed:', errInfo);
    }
  };

  const onChange = (pagination, filters, sorter, extra) => {
    console.log('params', pagination, filters, sorter, extra);
  }

  // const callFilter = async (value) => {
  //   const finalUrl = `${get(urls[0], "GET_CANDIDATES")}/status`
  //   const res = await requestPost(finalUrl, value);
  //   const payload = [value]
  //   console.log("resss",payload);
  // }

  const columns = [
    {
      title: 'Candidate Name',
      dataIndex: 'fullName',
      width: '15%',
      editable: true,
      render: (text, record) => (
        <Link to={`admin/viewcandidate/${record.id}`}>
          <span>{record.firstName} {record.lastName}</span>
            <span style={{float:"right"}}><EyeOutlined /></span>  
          </Link>),
    },
    {
      title: 'Candidate Email',
      dataIndex: 'email',
      width: '15%',
      editable: true,
    },
    {
      title: 'Job Title',
      dataIndex: 'jobTitle',
      width: '15%',
      editable: true,
    },
    {
      title: 'Interviewer Name',
      dataIndex: 'interviewer_name',
      width: '15%',
      editable: true,
    },
    // commented feedback column for admin pannel
    // { 
    //   title: 'Feedback',
    //   dataIndex: 'Feedback', 
    //   width: '20%',
    //   editable: false,
    //   render: (text,record) =>(
    //     <span>{record.candidateFeedback?.candidateHistory[0].feedBack} </span>)
    // },
    {
      title: 'Status',
      dataIndex: 'status',
      width: '10%',
      editable: true,
      filterMode: 'menu',
      filters: [
        {
          text: 'Applied',
          value: 'Applied',
        },
        {
          text: 'Short Listed',
          value: 'ShortListed',
        },
        {
          text: 'Rejected',
          value: 'NotShortListed',
        },
        {
          text: 'Schedule Round-1',
          value: 'ScheduleRound1',
        },
        {
          text: 'Reschedule Round-1',
          value: 'ReScheduleRound1',
        },
        {
          text: 'Selected Round-1',
          value: 'SelectedRound1',
        },
        {
          text: 'Not Selected Round-1',
          value: 'NotSelectedRound1',
        },
        {
          text: 'Schedule Round-2',
          value: 'ScheduleRound2',
        },
        {
          text: 'Reschedule Round-2',
          value: 'ReScheduleRound2',
        },
        {
          text: 'Selected Round-2',
          value: 'SelectedRound2',
        },
        {
          text: 'Not Selected Round-2',
          value: 'NotSelectedRound2',
        },
        {
          text: 'Schedule HR',
          value: 'ScheduleHr',
        },
        {
          text: 'Reschedule HR',
          value: 'ReScheduleHr',
        },
        {
          text: 'Offered',
          value: 'Offered',
        },
        {
          text: 'Accepted Offer',
          value: 'AcceptedOffer',
        },
        {
          text: 'Rejected Offer',
          value: 'RejectedOffer',
        },
        {
          text: 'Not Offered',
          value: 'NotOffered',
        }
        // {
        //   text: 'View All',
        //   value: '',
        // }
      ],
      onFilter: (value, record) => record.candidateFeedback?.status === value,
      render: (value, record) => (
        <span>
          {console.log('9999', record.candidateFeedback)}
          {record.candidateFeedback?.status}</span>
      )
    },
    {
      title: 'Action',
      dataIndex: 'Action',
      render: (text, record) => (
         <>
              <Link style={{"marginRight": "10px"}} to={`/admin/edit_candidate/${record.id}`}>
                <EditOutlined /></Link>
             <Typography.Link   onClick={() => handleDelete(record)}>
                <DeleteOutlined />
            </Typography.Link>
         
         </>),

    },
    // {
    //   title: 'Action',
    //   dataIndex: 'Action',
    //   render: (_, record) => {
    //     const editable = isEditing(record);
    //     return editable ? (
    //       <span>
    //         <Typography.Link
    //           onClick={() => save(record.key)}
    //           style={{
    //             marginRight: 8,
    //           }}
    //         >
    //           Save
    //         </Typography.Link>
    //         <Popconfirm title="Sure to cancel?" onConfirm={cancel}>
    //           <a>Cancel</a>
    //         </Popconfirm>
    //       </span>
    //     ) : (
    //       <>
    //         {/* <Typography.Link disabled={editingKey !== ''} onClick={() => edit(record)}>
    //           Edit
    //         </Typography.Link> */}
    //         <Typography.Link disabled={editingKey !== ''} onClick={() => handleDelete(record)}>
    //           Delete
    //         </Typography.Link>
    //       </>
    //     );
    //   },
    // },
  ];
  const mergedColumns = columns.map((col) => {
    if (!col.editable) {
      return col;
    }

    return {
      ...col,
      onCell: (record) => ({
        record,
        inputType: col.dataIndex === 'status' ? 'select' : 'text',
        dataIndex: col.dataIndex,
        title: col.title,
        editing: isEditing(record),
      }),
    };
  });

  return (
    <>      
      <Spin spinning={isload}>
        <Form form={form} component={false}>
          <Table
            components={{
              body: {
                cell: EditableCell,
              },
            }}
            bordered
            dataSource={candidates}
            columns={mergedColumns}
            rowClassName="editable-row"
            pagination={{
              onChange: cancel,
            }}
          />
        </Form>
      </Spin>
    </>
  );
};

export default CandidateLists;