// Packages
import { useContext, useState, useEffect} from 'react';
import {
    Form,
    Input,
    Select,
    Button,
    InputNumber,
    Row,
    Col,
    Upload,
    message,
    Alert,
    Layout,
    Spin
} from 'antd';
const { TextArea } = Input;

const  {Content} = Layout ;
import { UploadOutlined } from '@ant-design/icons';
import { RootContext } from '../../Context/RootContext';

// Styles
import styles from "../../styles/create_admin.module.css";
import { get, includes, isEmpty, map, min, split } from 'lodash';
import axios from 'axios';
import { request, requestPost } from '../../services/request';
import { urls } from '../../services/URLs';
import { BrowserRouter, Route } from 'react-router-dom';

const { Option } = Select;

const formItemLayout = {
    labelCol: {
        xs: {
            span: 24,
        },
        sm: {
            span: 8,
        },
    },
    wrapperCol: {
        xs: {
            span: 24,
        },
        sm: {
            span: 16,
        },
    },
};

const tailFormItemLayout = {
    wrapperCol: {
        xs: {
            span: 24,
            offset: 0,
        },
        sm: {
            span: 16,
            offset: 8,
        },
    },
};

const prefixSelector = (
    <Form.Item name="prefix" noStyle>
        <Select
            style={{
                width: 70,
            }}
        >
            <Option value="91">+91</Option>
        </Select>
    </Form.Item>
);

const props = {
    // https://stackoverflow.com/questions/51514757/action-function-is-required-with-antd-upload-control-but-i-dont-need-it
    
    beforeUpload: file => {
        
        const fileType = get(split(get(file, "name"), "."),"1");
        const isFile = includes([
            "doc",
            "docx",
            "pdf"
        ],fileType);

        if (!isFile) {
            message.error(`${file.name} is not a proper format file`);
        }
        return isFile || Upload.LIST_IGNORE;
    },
    onChange: info => {
        console.log(info.fileList);
    },
};

function CandidateForm() {

    const [form] = Form.useForm();
    const [expInMonth, setExpInMonth] = useState(0);
    const [expInYear, setExpInYear] = useState(0);
    const [relExpInMonth, setRelExpInMonth] = useState(0);
    const [relExpInYear, setRelExpInYear] = useState(0);
    const[iscreated,setiscreated] = useState({})
    const [isload,setisload] =useState(false)
    const [disablePhone, setDisablePhone] = useState(false);
    const [disableEmail, setDisableEmail] = useState(false);
    const [selectorsData, setSelectorsData] = useState([]);
    const [uploadResumeFile, setUploadResumeFile] = useState(null);
    const {setGetAllCandidate} = useContext(RootContext);

    useEffect(()=>{
       console.log("candidatForm... mount") 

       return()=>{
           console.log("candidatForm.. Unmount")
       }
    } )

    useEffect(async ()=>{
        const finalUrl = `${get(urls[0], "INTERVIEWER")}/selectors`
        const res = await request(finalUrl);
        setSelectorsData(res.data);
    },[])

    const renderOptions = selectorsData.map((value) => {
        return(
            <Option value={value.id}>{value.interviewerEmail}</Option>
        )
    })

    const validatePhoneAndEmail = async (e, type) => {
        if (!isEmpty(e.currentTarget.value)) {
          const finalUrl = `${get(urls[0], "VALIDATE_CANDIDATE")}${e.currentTarget.value}`;
          const res = await requestPost(finalUrl);
          if (res.data === "") {
            type === "phone" ? setDisablePhone(false) : setDisableEmail(false);
          } else {
            message.error("candidate already registered");
            type === "phone" ? setDisablePhone(true) : setDisableEmail(true);
          }
        }
      };

      const uploadResume = (e) => {
        let data = e.target.files[0];
        setUploadResumeFile(data);
      }

      const candidateLists = async () => {
        const candidatelist = await request(get(urls[0], "GET_CANDIDATES"))
        setGetAllCandidate(candidatelist.data)
      }
    
    const CreateCandidateHandler = async(values) => {
        setisload(true)
        // values['experience'] = `${expInYear}`;
        // values['relavantExperience'] = `${relExpInYear}`;
        try{
            const formData = new FormData();
            const ignoreFile = ["file", "experience", "relavantExperience", "lastName"];
            for(let key in values){
                if(ignoreFile.includes(key)){
                    continue;
                }
                formData.append(key, values[key]);
            }
            formData.append(
                "experience", expInYear
            )
            formData.append(
                "lastName", values.lastName
            )
            formData.append(
                "relavantExperience", relExpInYear
            )
            formData.append(
                "file", uploadResumeFile,
            )
            const res = await requestPost(get(urls[0],"CREATE_CANDIDATE"),formData,"post")
            
            if(res && res.status == 200){
                // this.props.history.push('/');
                // setiscreated({
                //     'message':"Candidate create succesfully.",
                //     'type':"success"
                // })
                message.success("Candidate create succesfully.")

                    // console.log("res---",res)
                    // const payload = {
                    //     "candidateId": res.data.id
                    // }
                    // const action ="/Applied"
                    // const finalurl = get(urls[1], "WORK_FLOW") + action
                    // const response = await requestPost(finalurl, payload, "post")
                    
                form.resetFields();
                setisload(false)
                candidateLists()
            }
            else{
                setisload(false)
                message.error("Candidate create  Failed.")
            }
        }
        catch(e){
            console.error(e)
            // setiscreated({
            //     'message':"Found eroor please try again",
            //     'type':"error"
            // })
            message.error("Found error please try again")
            setisload(false)
        }
        
        
    }

    const handleChange = (value) => {
        console.log(`selected ${value}`);
    }
    
    return (
        <Layout>
            {/* {!isEmpty(iscreated) && <Alert message={iscreated.message} type={iscreated.type} closable afterClose={handleClose} />} */}
            <Spin spinning={isload}>
            <Content>
                <Form
                    {...formItemLayout}
                    form={form}
                    name="create_candidate_form"
                    onFinish={CreateCandidateHandler}
                    initialValues={{
                        prefix: '91',
                        uploadResume: "fileList"
                    }}
                    scrollToFirstError
                    className={styles.create_admin_form}
                >

                    <Form.Item
                        name="firstName"
                        label="First Name"
                        tooltip="Name as per certificate"
                        rules={[
                            {
                                required: true,
                                message: 'Please enter valid First Name!',
                                whitespace: true,
                                max:50
                            },
                        ]}
                    >
                        <Input  autoComplete="new-name"  />
                    </Form.Item>

                    <Form.Item
                        name="lastName"
                        label="Last Name"
                        tooltip="Name as per certificate"
                        rules={[
                            {
                                required: true,
                                message: 'Please enter valid Last Name!',
                                max:50
                            }
                        ]}
                    >
                        <Input  autoComplete="new-name"  />
                    </Form.Item>

                    <Form.Item
                        name="phoneNumber"
                        label="Candidate Mobile no"
                        rules={[
                            {
                                required: true,
                                message: 'Please input valid Candidate mobile number!',
                                max:10,
                                min:10
                            },
                            // {validator: (rule,value,callback)=>{
                            //     console.log("hello")
                            //     if(disablePhone){
                            //          return Promise.reject("Error num alerdy a=exist")
                            //     }
                            // }}
                        ]}
                    >
                        <Input  autoComplete="new-name"
                            addonBefore={prefixSelector}
                            style={{
                                width: '100%',
                            }}
                            type='number'
                            onBlur={(e) => validatePhoneAndEmail(e, "phone")}
                        />
                    </Form.Item>

                    <Form.Item
                        name="email"
                        label="Candidate E-mail"
                        rules={[
                            {
                                type: 'email',
                                message: 'The input is not valid E-mail!',
                            },
                            {
                                required: true,
                                message: 'Please input your E-mail!',
                            },
                        ]}
                    >
                        <Input  autoComplete="new-name" onBlur={(e) => validatePhoneAndEmail(e, "email")} />
                    </Form.Item>

                    <Form.Item
                        name="skills"
                        label="Skill Sets"
                        tooltip="Write skills with comma"
                        rules={[
                            {
                                required: true,
                                message: 'Please enter Skill Sets!',
                                whitespace: true,
                            },
                        ]}
                    >
                        <Input  autoComplete="new-name" />
                    </Form.Item>

                    <Form.Item
                        name="jobTitle"
                        label="Job Title"
                        rules={[
                            {
                                required: true,
                                message: 'Please enter Job Title!',
                                whitespace: true,
                                max: 50
                            },
                        ]}
                    >
                        <Input  autoComplete="new-name" />
                    </Form.Item>

                    <Form.Item
                        name="jobDescription"
                        label="Job Description"
                        rules={[
                            {
                                required: true,
                                message: 'Please Enter description !'
                            },
                        ]}
                    >
                        <TextArea rows={3} />
                    </Form.Item>

                    <Form.Item
                        name="experience"
                        label="Total Experience"
                        rules={[
                            {
                                required: false,
                                message: 'Please enter Total Experience!',
                                whitespace: true,
                            },
                        ]}
                    >
                        <Input.Group size="large">
                            <Row gutter={16}>
                                <Col span={10}>
                                    <InputNumber onChange={(value) => setExpInYear(value)} addonAfter="Years" min="0" step="0.1" />
                                </Col>
                                {/* <Col span={10}>
                                    <InputNumber onChange={(value) => setExpInMonth(value)} addonAfter="Months" value={expInMonth} />
                                </Col> */}
                            </Row>
                        </Input.Group>
                    </Form.Item>

                    <Form.Item
                        name="relavantExperience"
                        label="Relavant Experience"
                        rules={[
                            {
                                required: false,
                                message: 'Please input relavant Experience',
                            },
                        ]}
                    >
                        <Input.Group size="large">
                            <Row gutter={16}>
                                <Col span={10}>
                                    <InputNumber onChange={(value) => setRelExpInYear(value)} addonAfter="Years"   min="0" step="0.1"  />
                                </Col>
                                {/* <Col span={10}>
                                    <InputNumber onChange={(value) => setRelExpInMonth(value)} addonAfter="Months" value={relExpInMonth} />
                                </Col> */}
                            </Row>
                        </Input.Group>
                    </Form.Item>

                    <Form.Item
                    name="file"
                    label="Upload Resume"
                    rules={[]}
                >
                    {/* <Upload {...props}>
                        <Button icon={<UploadOutlined />}>Upload pdf/word only</Button>
                    </Upload> */}
                    <input type="file" id="myfile" name="myfile" onChange={(e)=>uploadResume(e)}></input>
                </Form.Item>

                <Form.Item
                    name="selectorId"
                    label="Select Interviewer"
                    rules={[
                        {
                            required: true,
                            message: 'Please Select Interviewer',
                        },
                    ]}
                    >
                    <Select  style={{ width: 200 }} onChange={handleChange}>
                        {renderOptions}
                    </Select>
                </Form.Item>

                    <Form.Item {...tailFormItemLayout}>
                        <Button type="primary" htmlType="submit" disabled={disableEmail || disablePhone}>
                            Save candidate
                        </Button>
                    </Form.Item>
                </Form>
            </Content>
            </Spin>

        </Layout>
    )
}

export default CandidateForm;
